},

getActivity(){
},
log (str1, str2 = '') {
console.log(str1, str2)
},
showPlugin () {
this.$vux.datetime.show({
cancelText: '取消',
confirmText: '确定',
format: 'YYYY-MM-DD HH',
value: '2017-05-20 18',
onConfirm (val) {
console.log('plugin confirm', val)
},
onShow () {
console.log('plugin show')
},
onHide () {
console.log('plugin hide')
}
})
},
toggleFormat () {
this.format = this.format === 'YYYY-MM-DD HH:mm' ? 'YYYY-MM-DD' : 'YYYY-MM-DD HH:mm'
},
changeStart (value) {
this.startTime = value;
},
changeEnd (value) {
this.endTime = value;
},
clearValue (value) {
this.value6 = ''
},
clearValue8 (value) {
this.value8 = ''
},
setToday (value) {
let now = new Date();
let cmonth = now.getMonth() + 1;
let day = now.getDate();
if (cmonth < 10) cmonth = '0' + cmonth;
if (day < 10) day = '0' + day;
this.value7 = now.getFullYear() + '-' + cmonth + '-' + day;
console.log('set today ok')
},

chooseImage(it) {
wx.chooseImage({
count: 1, // 默认9
sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
success: res => {
let localIds = res.localIds || [];
new Promise(resolve => {
let serverIds = [];
let toUpload = localId =>
wx.uploadImage({
localId: localId, // 需要上传的图片的本地ID，由chooseImage接口获得
isShowProgressTips: 1, // 默认为1，显示进度提示
success: res => {
serverIds.push(res.serverId);
if (localIds.length) {
toUpload(localIds.shift());
} else {
resolve(serverIds);
}
}
});
if (localIds.length) {
toUpload(localIds.shift());
} else {
resolve(serverIds);
}
}).then(serverIds => {
let promiseList = [];
serverIds.forEach(serverId =>
promiseList.push(
this.$http.get('picture/upload', {
params: {
mediaId: serverId
}
})
)
);
Promise.all(promiseList).then(result => {
let pictureIds = [];
result.forEach(item => pictureIds.push(item.data));
it.list.push(imgBaseUrl + pictureIds.join());
it.arr.push(pictureIds.join());
this.testText = result;
});
});
}
});
},
...mapActions(['setInfo'])
},
computed: {
...mapGetters(['info'])
},
mounted() {
weixin.init(['chooseImage', 'uploadImage']);
this.getActivity();
this.getList();
this.getUser();
function userDate(uData){
let myDate = new Date(uData);
let year = myDate.getFullYear();
let months = myDate.getMonth() + 1;
if(months < 10){
months = '0' + months;
}
let day = myDate.getDate();
if(day < 10){
day = '0' + day;
}
let hours=myDate.getHours();
if(hours < 10){
hours = '0' + hours;
}
let minutes=myDate.getMinutes();
if(minutes < 10){
minutes = '0' + minutes;
}
let second=myDate.getSeconds();
return year + '-' + months + '-' + day+'  '+hours +':'+ minutes;
}
if (this.info.studyid) {
this.startTime = userDate(this.info.createtime);
this.endTime = userDate(this.info.endtime);
this.activeContent = this.info.content;
const pics = this.info.pictures;
if (pics.length) {
this.picList.list = pics.map(item => `${imgBaseUrl}${item.pictureId}`);
this.picList.arr = pics.map(item => item.pictureId);
}
console.log(this.info.pictures[0].pictureId);
}
},
beforeDestroy () {
this.setInfo({});
}
};



